import { FunctionComponent, useMemo, type CSSProperties } from "react";
import styles from "./AccordionItem.module.css";

export type AccordionItemType = {
  question?: string;

  /** Style props */
  propWidth?: CSSProperties["width"];
  propMinWidth?: CSSProperties["minWidth"];
};

const AccordionItem: FunctionComponent<AccordionItemType> = ({
  question,
  propWidth,
  propMinWidth,
}) => {
  const accordionItemStyle: CSSProperties = useMemo(() => {
    return {
      width: propWidth,
    };
  }, [propWidth]);

  const frameDivStyle: CSSProperties = useMemo(() => {
    return {
      minWidth: propMinWidth,
    };
  }, [propMinWidth]);

  return (
    <div className={styles.accordionItem} style={accordionItemStyle}>
      <div className={styles.divider} />
      <div className={styles.title}>
        <div className={styles.questionWrapper} style={frameDivStyle}>
          <b className={styles.question}>{question}</b>
        </div>
        <img className={styles.icon} alt="" src="/icon-2.svg" />
      </div>
    </div>
  );
};

export default AccordionItem;
