import { FunctionComponent, useMemo, type CSSProperties } from "react";
import styles from "./Column.module.css";

export type ColumnType = {
  placeholderImage?: string;
  heading?: string;
  pathSplitter?: string;

  /** Style props */
  propLineHeight?: CSSProperties["lineHeight"];
  propTextAlign?: CSSProperties["textAlign"];
  propTextAlign1?: CSSProperties["textAlign"];
};

const Column: FunctionComponent<ColumnType> = ({
  placeholderImage,
  heading,
  pathSplitter,
  propLineHeight,
  propTextAlign,
  propTextAlign1,
}) => {
  const heading1Style: CSSProperties = useMemo(() => {
    return {
      lineHeight: propLineHeight,
      textAlign: propTextAlign,
    };
  }, [propLineHeight, propTextAlign]);

  const pathSplitterStyle: CSSProperties = useMemo(() => {
    return {
      textAlign: propTextAlign1,
    };
  }, [propTextAlign1]);

  return (
    <div className={styles.column}>
      <img
        className={styles.placeholderImageIcon}
        loading="lazy"
        alt=""
        src={placeholderImage}
      />
      <div className={styles.content}>
        <h1 className={styles.heading} style={heading1Style}>
          {heading}
        </h1>
        <div className={styles.pathSplitter} style={pathSplitterStyle}>
          {pathSplitter}
        </div>
      </div>
    </div>
  );
};

export default Column;
