import { FunctionComponent, useMemo, type CSSProperties } from "react";
import styles from "./Footer.module.css";

export type FooterType = {
  linkTwo?: string;
  linkThree?: string;
  linkFour?: string;
  linkFive?: string;
  relumeAllRightsReserved?: string;
  privacyPolicy?: string;
  termsOfService?: string;
  cookiesSettings?: string;

  /** Style props */
  propWidth?: CSSProperties["width"];
  propMinWidth?: CSSProperties["minWidth"];
  propMinWidth1?: CSSProperties["minWidth"];
  propMinWidth2?: CSSProperties["minWidth"];
  propMinWidth3?: CSSProperties["minWidth"];
  propMinWidth4?: CSSProperties["minWidth"];
  propMinWidth5?: CSSProperties["minWidth"];
  propMinWidth6?: CSSProperties["minWidth"];
};

const Footer: FunctionComponent<FooterType> = ({
  linkTwo,
  linkThree,
  linkFour,
  linkFive,
  relumeAllRightsReserved,
  privacyPolicy,
  termsOfService,
  cookiesSettings,
  propWidth,
  propMinWidth,
  propMinWidth1,
  propMinWidth2,
  propMinWidth3,
  propMinWidth4,
  propMinWidth5,
  propMinWidth6,
}) => {
  const content1Style: CSSProperties = useMemo(() => {
    return {
      width: propWidth,
    };
  }, [propWidth]);

  const linkTwoStyle: CSSProperties = useMemo(() => {
    return {
      minWidth: propMinWidth,
    };
  }, [propMinWidth]);

  const linkThreeStyle: CSSProperties = useMemo(() => {
    return {
      minWidth: propMinWidth1,
    };
  }, [propMinWidth1]);

  const linkFiveStyle: CSSProperties = useMemo(() => {
    return {
      minWidth: propMinWidth2,
    };
  }, [propMinWidth2]);

  const relumeAllRightsStyle: CSSProperties = useMemo(() => {
    return {
      minWidth: propMinWidth3,
    };
  }, [propMinWidth3]);

  const privacyPolicyStyle: CSSProperties = useMemo(() => {
    return {
      minWidth: propMinWidth4,
    };
  }, [propMinWidth4]);

  const termsOfServiceStyle: CSSProperties = useMemo(() => {
    return {
      minWidth: propMinWidth5,
    };
  }, [propMinWidth5]);

  const cookiesSettingsStyle: CSSProperties = useMemo(() => {
    return {
      minWidth: propMinWidth6,
    };
  }, [propMinWidth6]);

  return (
    <section className={styles.footer7}>
      <div className={styles.errorHandler}>
        <div className={styles.content} style={content1Style}>
          <div className={styles.functionCaller}>
            <div className={styles.logo}>
              <img
                className={styles.colorDark}
                loading="lazy"
                alt=""
                src="/color--dark.svg"
              />
            </div>
          </div>
          <div className={styles.links}>
            <div className={styles.linkOne}>About Us</div>
            <div className={styles.linkTwo} style={linkTwoStyle}>
              {linkTwo}
            </div>
            <div className={styles.linkThree} style={linkThreeStyle}>
              {linkThree}
            </div>
            <div className={styles.linkFour}>{linkFour}</div>
            <div className={styles.linkFive} style={linkFiveStyle}>
              {linkFive}
            </div>
          </div>
        </div>
      </div>
      <footer className={styles.credits}>
        <div className={styles.divider} />
        <div className={styles.row}>
          <div className={styles.relumeAllRights} style={relumeAllRightsStyle}>
            {relumeAllRightsReserved}
          </div>
          <div className={styles.footerLinks}>
            <div className={styles.privacyPolicy} style={privacyPolicyStyle}>
              {privacyPolicy}
            </div>
            <div className={styles.termsOfService} style={termsOfServiceStyle}>
              {termsOfService}
            </div>
            <div
              className={styles.cookiesSettings}
              style={cookiesSettingsStyle}
            >
              {cookiesSettings}
            </div>
          </div>
        </div>
      </footer>
    </section>
  );
};

export default Footer;
