import { FunctionComponent, useMemo, type CSSProperties } from "react";
import styles from "./ListItem.module.css";

export type ListItemType = {
  heading?: string;
  text?: string;
  button?: string;

  /** Style props */
  propMinWidth?: CSSProperties["minWidth"];
};

const ListItem: FunctionComponent<ListItemType> = ({
  heading,
  text,
  button,
  propMinWidth,
}) => {
  const button4Style: CSSProperties = useMemo(() => {
    return {
      minWidth: propMinWidth,
    };
  }, [propMinWidth]);

  return (
    <div className={styles.listItem}>
      <img
        className={styles.iconRelume}
        loading="lazy"
        alt=""
        src="/icon--relume-81.svg"
      />
      <h3 className={styles.heading}>{heading}</h3>
      <div className={styles.text}>{text}</div>
      <div className={styles.action}>
        <div className={styles.stylelinkSmallfalseDark}>
          <div className={styles.button} style={button4Style}>
            {button}
          </div>
          <img
            className={styles.iconChevronRight}
            loading="lazy"
            alt=""
            src="/icon--chevronright.svg"
          />
        </div>
      </div>
    </div>
  );
};

export default ListItem;
