import { FunctionComponent, useMemo, type CSSProperties } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { Button } from "react-bootstrap";
import styles from "./CTA.module.css";

export type CTAType = {
  heading?: string;
  stringManipulation?: string;
  button?: string;
  button1?: string;

  /** Style props */
  propPadding?: CSSProperties["padding"];
  propTextAlign?: CSSProperties["textAlign"];
  propTextAlign1?: CSSProperties["textAlign"];
  propWidth?: CSSProperties["width"];
  propMinWidth?: CSSProperties["minWidth"];
  propMinWidth1?: CSSProperties["minWidth"];
};

const CTA: FunctionComponent<CTAType> = ({
  heading,
  stringManipulation,
  button,
  button1,
  propPadding,
  propTextAlign,
  propTextAlign1,
  propWidth,
  propMinWidth,
  propMinWidth1,
}) => {
  const cTA19Style: CSSProperties = useMemo(() => {
    return {
      padding: propPadding,
    };
  }, [propPadding]);

  const headingStyle: CSSProperties = useMemo(() => {
    return {
      textAlign: propTextAlign,
    };
  }, [propTextAlign]);

  const stringManipulationStyle: CSSProperties = useMemo(() => {
    return {
      textAlign: propTextAlign1,
    };
  }, [propTextAlign1]);

  const stylePrimarySmallFalseDaStyle: CSSProperties = useMemo(() => {
    return {
      width: propWidth,
    };
  }, [propWidth]);

  const button3Style: CSSProperties = useMemo(() => {
    return {
      minWidth: propMinWidth,
    };
  }, [propMinWidth]);

  return (
    <section className={styles.cta19} style={cTA19Style}>
      <div className={styles.column}>
        <div className={styles.content}>
          <h1 className={styles.heading} style={headingStyle}>
            {heading}
          </h1>
          <div
            className={styles.stringManipulation}
            style={stringManipulationStyle}
          >
            {stringManipulation}
          </div>
        </div>
        <div className={styles.actions}>
          <button
            className={styles.styleprimarySmallfalseDa}
            style={stylePrimarySmallFalseDaStyle}
          >
            <div className={styles.button} style={button3Style}>
              {button}
            </div>
          </button>
          <Button variant="primary" />
        </div>
      </div>
    </section>
  );
};

export default CTA;
