import { FunctionComponent, useMemo, type CSSProperties } from "react";
import styles from "./Card1.module.css";

export type Card1Type = {
  placeholderImage?: string;
  text?: string;
  heading?: string;
  shapeToolsPanel?: string;
  placeholderImage1?: string;
  text1?: string;

  /** Style props */
  propFlex?: CSSProperties["flex"];
  propMinWidth?: CSSProperties["minWidth"];
  propLineHeight?: CSSProperties["lineHeight"];
};

const Card1: FunctionComponent<Card1Type> = ({
  placeholderImage,
  text,
  heading,
  shapeToolsPanel,
  placeholderImage1,
  text1,
  propFlex,
  propMinWidth,
  propLineHeight,
}) => {
  const cardStyle: CSSProperties = useMemo(() => {
    return {
      flex: propFlex,
      minWidth: propMinWidth,
    };
  }, [propFlex, propMinWidth]);

  const heading2Style: CSSProperties = useMemo(() => {
    return {
      lineHeight: propLineHeight,
    };
  }, [propLineHeight]);

  return (
    <div className={styles.card} style={cardStyle}>
      <img
        className={styles.placeholderImageIcon}
        loading="lazy"
        alt=""
        src={placeholderImage}
      />
      <div className={styles.content}>
        <div className={styles.content1}>
          <div className={styles.text}>{text}</div>
          <h3 className={styles.heading} style={heading2Style}>
            {heading}
          </h3>
          <div className={styles.shapeToolsPanel}>{shapeToolsPanel}</div>
        </div>
        <div className={styles.avatar}>
          <img
            className={styles.placeholderImageIcon1}
            loading="lazy"
            alt=""
            src={placeholderImage1}
          />
          <div className={styles.content2}>
            <div className={styles.text1}>{text1}</div>
            <div className={styles.time}>
              <div className={styles.dataAggregator}>11 Jan 2022</div>
              <div className={styles.div}>•</div>
              <div className={styles.inputFilter}>5 min read</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Card1;
