import { FunctionComponent, useMemo, type CSSProperties } from "react";
import styles from "./Content7.module.css";

export type Content7Type = {
  iconEnvelope?: string;
  heading?: string;
  text?: string;
  link?: string;

  /** Style props */
  propMinWidth?: CSSProperties["minWidth"];
};

const Content7: FunctionComponent<Content7Type> = ({
  iconEnvelope,
  heading,
  text,
  link,
  propMinWidth,
}) => {
  const content3Style: CSSProperties = useMemo(() => {
    return {
      minWidth: propMinWidth,
    };
  }, [propMinWidth]);

  return (
    <div className={styles.content} style={content3Style}>
      <img
        className={styles.iconEnvelope}
        loading="lazy"
        alt=""
        src={iconEnvelope}
      />
      <div className={styles.contactInfo}>
        <div className={styles.content1}>
          <h1 className={styles.heading}>{heading}</h1>
          <div className={styles.text}>{text}</div>
        </div>
        <div className={styles.link}>{link}</div>
      </div>
    </div>
  );
};

export default Content7;
