import { FunctionComponent, useMemo, type CSSProperties } from "react";
import styles from "./Content2.module.css";

export type Content2Type = {
  heading?: string;
  price?: string;

  /** Style props */
  propFlex?: CSSProperties["flex"];
  propHeight?: CSSProperties["height"];
  propWidth?: CSSProperties["width"];
  propMinWidth?: CSSProperties["minWidth"];
};

const Content2: FunctionComponent<Content2Type> = ({
  heading,
  price,
  propFlex,
  propHeight,
  propWidth,
  propMinWidth,
}) => {
  const content2Style: CSSProperties = useMemo(() => {
    return {
      flex: propFlex,
      height: propHeight,
    };
  }, [propFlex, propHeight]);

  const heading3Style: CSSProperties = useMemo(() => {
    return {
      width: propWidth,
      minWidth: propMinWidth,
    };
  }, [propWidth, propMinWidth]);

  return (
    <div className={styles.content} style={content2Style}>
      <img className={styles.iconRelume} alt="" src="/icon--relume-81.svg" />
      <div className={styles.price}>
        <b className={styles.heading} style={heading3Style}>
          {heading}
        </b>
        <div className={styles.container}>
          <b className={styles.price1}>{price}</b>
          <b className={styles.price2} />
        </div>
      </div>
    </div>
  );
};

export default Content2;
