import { FunctionComponent, useMemo, type CSSProperties } from "react";
import styles from "./AccordionItem1.module.css";

export type AccordionItem1Type = {
  details?: string;
  paragraph?: string;

  /** Style props */
  propMinWidth?: CSSProperties["minWidth"];
};

const AccordionItem1: FunctionComponent<AccordionItem1Type> = ({
  details,
  paragraph,
  propMinWidth,
}) => {
  const detailsStyle: CSSProperties = useMemo(() => {
    return {
      minWidth: propMinWidth,
    };
  }, [propMinWidth]);

  return (
    <div className={styles.accordionItem}>
      <div className={styles.divider} />
      <div className={styles.title}>
        <div className={styles.details} style={detailsStyle}>
          {details}
        </div>
        <div className={styles.plusWrapper}>
          <img className={styles.plusIcon} alt="" src="/plus.svg" />
        </div>
      </div>
      <div className={styles.paragraph}>{paragraph}</div>
    </div>
  );
};

export default AccordionItem1;
