import { useEffect } from "react";
import {
  Routes,
  Route,
  useNavigationType,
  useLocation,
} from "react-router-dom";
import DataHub from "./pages/DataHub";
import DesktopPage from "./pages/DesktopPage";
import DesktopPage1 from "./pages/DesktopPage1";
import HerbalDesktop from "./pages/HerbalDesktop";
import HerbFinder from "./pages/HerbFinder";

function App() {
  const action = useNavigationType();
  const location = useLocation();
  const pathname = location.pathname;

  useEffect(() => {
    if (action !== "POP") {
      window.scrollTo(0, 0);
    }
  }, [action, pathname]);

  useEffect(() => {
    let title = "";
    let metaDescription = "";

    switch (pathname) {
      case "/":
        title = "";
        metaDescription = "";
        break;
      case "/desktop-page-2":
        title = "";
        metaDescription = "";
        break;
      case "/desktop-page-3":
        title = "";
        metaDescription = "";
        break;
      case "/herbal-desktop-4":
        title = "";
        metaDescription = "";
        break;
      case "/herb-finder":
        title = "";
        metaDescription = "";
        break;
    }

    if (title) {
      document.title = title;
    }

    if (metaDescription) {
      const metaDescriptionTag: HTMLMetaElement | null = document.querySelector(
        'head > meta[name="description"]'
      );
      if (metaDescriptionTag) {
        metaDescriptionTag.content = metaDescription;
      }
    }
  }, [pathname]);

  return (
    <Routes>
      <Route path="/" element={<DataHub />} />
      <Route path="/desktop-page-2" element={<DesktopPage />} />
      <Route path="/desktop-page-3" element={<DesktopPage1 />} />
      <Route path="/herbal-desktop-4" element={<HerbalDesktop />} />
      <Route path="/herb-finder" element={<HerbFinder />} />
    </Routes>
  );
}
export default App;
