import { FunctionComponent, useMemo, type CSSProperties } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { Button } from "react-bootstrap";
import styles from "./Content.module.css";

export type ContentType = {
  subheading?: string;
  heading?: string;
  mergeMatrices?: string;
  button?: string;

  /** Style props */
  propAlignSelf?: CSSProperties["alignSelf"];
  propFlex?: CSSProperties["flex"];
  propPadding?: CSSProperties["padding"];
  propMinWidth?: CSSProperties["minWidth"];
  propWidth?: CSSProperties["width"];
  propMinWidth1?: CSSProperties["minWidth"];
  propMinWidth2?: CSSProperties["minWidth"];
};

const Content: FunctionComponent<ContentType> = ({
  subheading,
  heading,
  mergeMatrices,
  button,
  propAlignSelf,
  propFlex,
  propPadding,
  propMinWidth,
  propWidth,
  propMinWidth1,
  propMinWidth2,
}) => {
  const contentStyle: CSSProperties = useMemo(() => {
    return {
      alignSelf: propAlignSelf,
      flex: propFlex,
      padding: propPadding,
      minWidth: propMinWidth,
    };
  }, [propAlignSelf, propFlex, propPadding, propMinWidth]);

  const subheadingStyle: CSSProperties = useMemo(() => {
    return {
      width: propWidth,
      minWidth: propMinWidth1,
    };
  }, [propWidth, propMinWidth1]);

  const buttonStyle: CSSProperties = useMemo(() => {
    return {
      minWidth: propMinWidth2,
    };
  }, [propMinWidth2]);

  return (
    <div className={styles.content} style={contentStyle}>
      <div className={styles.sectionTitle}>
        <div className={styles.subheading} style={subheadingStyle}>
          {subheading}
        </div>
        <div className={styles.content1}>
          <h1 className={styles.heading}>{heading}</h1>
          <div className={styles.mergeMatrices}>{mergeMatrices}</div>
        </div>
      </div>
      <div className={styles.actions}>
        <Button variant="primary" />
        <div className={styles.stylelinkSmallfalseDark}>
          <div className={styles.button} style={buttonStyle}>
            {button}
          </div>
          <img
            className={styles.iconChevronRight}
            alt=""
            src="/icon--chevronright.svg"
          />
        </div>
      </div>
    </div>
  );
};

export default Content;
