import { FunctionComponent, useMemo, type CSSProperties } from "react";
import styles from "./Container.module.css";

export type ContainerType = {
  quote?: string;
  functionCall?: string;
  variableStorage?: string;

  /** Style props */
  propMinWidth?: CSSProperties["minWidth"];
};

const Container: FunctionComponent<ContainerType> = ({
  quote,
  functionCall,
  variableStorage,
  propMinWidth,
}) => {
  const functionCallStyle: CSSProperties = useMemo(() => {
    return {
      minWidth: propMinWidth,
    };
  }, [propMinWidth]);

  return (
    <div className={styles.container}>
      <div className={styles.stars}>
        <img
          className={styles.shapeRotatorIcon}
          alt=""
          src="/shape-rotator.svg"
        />
        <img className={styles.vectorIcon} alt="" src="/shape-rotator.svg" />
        <img
          className={styles.symbolScalerIcon}
          alt=""
          src="/shape-rotator.svg"
        />
        <img className={styles.vectorIcon1} alt="" src="/shape-rotator.svg" />
        <img
          className={styles.alignmentGuideIcon}
          alt=""
          src="/shape-rotator.svg"
        />
      </div>
      <b className={styles.quote}>{quote}</b>
      <div className={styles.avatar}>
        <div className={styles.avatarImageWrapper}>
          <img
            className={styles.avatarImageIcon}
            loading="lazy"
            alt=""
            src="/avatar-image@2x.png"
          />
        </div>
        <div className={styles.iterativeLoop}>
          <div className={styles.avatarContent}>
            <div className={styles.functionCall} style={functionCallStyle}>
              {functionCall}
            </div>
            <div className={styles.variableStorage}>{variableStorage}</div>
          </div>
        </div>
        <div className={styles.divider} />
        <div className={styles.logo2ColordarkWrapper}>
          <img
            className={styles.logo2ColordarkIcon}
            loading="lazy"
            alt=""
            src="/logo2-colordark.svg"
          />
        </div>
      </div>
    </div>
  );
};

export default Container;
