import { FunctionComponent, useMemo, type CSSProperties } from "react";
import styles from "./Actions.module.css";

export type ActionsType = {
  button?: string;
  button1?: string;

  /** Style props */
  propWidth?: CSSProperties["width"];
  propMinWidth?: CSSProperties["minWidth"];
  propMinWidth1?: CSSProperties["minWidth"];
};

const Actions: FunctionComponent<ActionsType> = ({
  button,
  button1,
  propWidth,
  propMinWidth,
  propMinWidth1,
}) => {
  const styleSecondarySmallFalseStyle: CSSProperties = useMemo(() => {
    return {
      width: propWidth,
    };
  }, [propWidth]);

  const button1Style: CSSProperties = useMemo(() => {
    return {
      minWidth: propMinWidth,
    };
  }, [propMinWidth]);

  const button2Style: CSSProperties = useMemo(() => {
    return {
      minWidth: propMinWidth1,
    };
  }, [propMinWidth1]);

  return (
    <div className={styles.actions}>
      <button
        className={styles.stylesecondarySmallfalse}
        style={styleSecondarySmallFalseStyle}
      >
        <div className={styles.button} style={button1Style}>
          {button}
        </div>
      </button>
      <div className={styles.stylelinkSmallfalseDark}>
        <div className={styles.button1} style={button2Style}>
          {button1}
        </div>
        <img
          className={styles.iconChevronRight}
          alt=""
          src="/icon--chevronright.svg"
        />
      </div>
    </div>
  );
};

export default Actions;
