import { FunctionComponent, useMemo, type CSSProperties } from "react";
import styles from "./Content6.module.css";

export type Content6Type = {
  placeholderImage?: string;

  /** Style props */
  propHeight?: CSSProperties["height"];
};

const Content6: FunctionComponent<Content6Type> = ({
  placeholderImage,
  propHeight,
}) => {
  const placeholderImageIconStyle: CSSProperties = useMemo(() => {
    return {
      height: propHeight,
    };
  }, [propHeight]);

  return (
    <div className={styles.content}>
      <img
        className={styles.placeholderImageIcon}
        alt=""
        src={placeholderImage}
        style={placeholderImageIconStyle}
      />
      <div className={styles.captionFigure}>
        <div className={styles.errorHandler} />
        <div className={styles.imageCaptionGoes}>Image caption goes here</div>
      </div>
    </div>
  );
};

export default Content6;
