import { FunctionComponent, useMemo, type CSSProperties } from "react";
import styles from "./Card2.module.css";

export type Card2Type = {
  placeholderImage?: string;
  queueManager?: string;
  stackManager?: string;
  heading?: string;
  dictionaries?: string;

  /** Style props */
  propMinWidth?: CSSProperties["minWidth"];
  propPadding?: CSSProperties["padding"];
  propMinWidth1?: CSSProperties["minWidth"];
  propMinWidth2?: CSSProperties["minWidth"];
  propLineHeight?: CSSProperties["lineHeight"];
  propAlignSelf?: CSSProperties["alignSelf"];
};

const Card2: FunctionComponent<Card2Type> = ({
  placeholderImage,
  queueManager,
  stackManager,
  heading,
  dictionaries,
  propMinWidth,
  propPadding,
  propMinWidth1,
  propMinWidth2,
  propLineHeight,
  propAlignSelf,
}) => {
  const card1Style: CSSProperties = useMemo(() => {
    return {
      minWidth: propMinWidth,
    };
  }, [propMinWidth]);

  const containerStyle: CSSProperties = useMemo(() => {
    return {
      padding: propPadding,
    };
  }, [propPadding]);

  const queueManagerStyle: CSSProperties = useMemo(() => {
    return {
      minWidth: propMinWidth1,
    };
  }, [propMinWidth1]);

  const stackManagerStyle: CSSProperties = useMemo(() => {
    return {
      minWidth: propMinWidth2,
    };
  }, [propMinWidth2]);

  const heading4Style: CSSProperties = useMemo(() => {
    return {
      lineHeight: propLineHeight,
      alignSelf: propAlignSelf,
    };
  }, [propLineHeight, propAlignSelf]);

  return (
    <div className={styles.card} style={card1Style}>
      <img
        className={styles.placeholderImageIcon}
        alt=""
        src={placeholderImage}
      />
      <div className={styles.content}>
        <div className={styles.info}>
          <div className={styles.container} style={containerStyle}>
            <div className={styles.queueManager} style={queueManagerStyle}>
              {queueManager}
            </div>
          </div>
          <div className={styles.stackManager} style={stackManagerStyle}>
            {stackManager}
          </div>
        </div>
        <div className={styles.content1}>
          <h3 className={styles.heading} style={heading4Style}>
            {heading}
          </h3>
          <div className={styles.dictionaries}>{dictionaries}</div>
        </div>
      </div>
      <div className={styles.stylelinkSmallfalseDark}>
        <div className={styles.button}>Read More</div>
        <img
          className={styles.iconChevronRight}
          alt=""
          src="/icon--chevronright.svg"
        />
      </div>
    </div>
  );
};

export default Card2;
